const Airdrop = () => {
  const [activeTab, setActiveTab] = React.useState('UPCOMING');

  const handleTabChange = (tabValue) => {
    setActiveTab(tabValue);
  };

  return (
    <div className={styles.Airdrop}>
      <div className={styles.banner}>
        <div className={styles.silkBelt} />
        <div className={styles.airdropImage} />
        <div className={styles.airdropText}>
          <div className={styles.airdropTextHorizon}>Horizon</div>
          <div className={styles.airdropTextAirdrop}>Airdrop</div>
        </div>
      </div>
      <div className={styles.container}>
        <Tab onChange={handleTabChange}>
          <TabLabel>UPCOMING</TabLabel>
          <TabLabel>ONGOING</TabLabel>
          <TabLabel>ENDED</TabLabel>
        </Tab>
        <MainContainer className={styles.cardContainer}>
          <Row gutter={[16, 16]}>
            {AIRDROPS.filter(airdrop => airdrop.status === activeTab).map((airdrop) => (
              <Col span={24} sm={12} md={8} lg={6} key={airdrop.id}>
                <SquareCard name={airdrop.title} picUrl={airdrop.bannerImg} />
              </Col>
            ))}
          </Row>
        </MainContainer>
      </div>
    </div>
  );
};

export default Airdrop;
